.hint-display-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

.hint-display {
  background: white;
  padding: 60px 80px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease;
}

.hint-word {
  font-size: 4rem;
  font-weight: bold;
  color: #667eea;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.hint-number {
  font-size: 5rem;
  font-weight: bold;
  color: #764ba2;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .hint-display {
    padding: 40px 50px;
  }
  
  .hint-word {
    font-size: 2.5rem;
  }
  
  .hint-number {
    font-size: 3rem;
  }
}